<template>
  <v-container>
    <v-card-title>Âm dương lịch</v-card-title>
    <v-card-subtitle><span class="font-italic">by kafka2405</span></v-card-subtitle>
    <v-row class="pt-5 mx-5 pb-0">
      <v-col
          cols="12"
          class="pb-0"
      >
        <v-select
            autofocus
            v-model="chosenTimeObject.year"
            label="Chọn năm"
            outlined
            :items="fixedTimeObject.years"
            dense
            menu-props="auto"
            hide-details
        ></v-select>
      </v-col>
      <v-col
          cols="12"
          class="pb-0"
      >
        <v-select
            v-model="chosenTimeObject.month"
            label="Chọn tháng"
            outlined
            :items="fixedTimeObject.monthsOfYear"
            dense
            hide-details
            menu-props="auto"
        ></v-select>
      </v-col>
      <v-col
          cols="12"
          class="pb-0"
      >
        <v-select
            v-model="chosenTimeObject.day"
            label="Chọn ngày"
            outlined
            :items="fixedTimeObject.daysOfMonth"
            dense
            hide-details
            required
            :rules="dayRules"
            menu-props="auto"
        ></v-select>
      </v-col>
      <v-col cols="12"
             class="pb-0"
      >
        <v-radio-group
            column
            v-model="option"
        >
          <v-radio
              label="Dương lịch sang âm lịch"
              value="1"
          ></v-radio>
          <v-radio
              label="Âm lịch sang dương lịch"
              value="2"
          ></v-radio>
        </v-radio-group>
      </v-col>
      <v-col cols="12">
        <v-btn
        outlined
        rounded
        :disabled="!chosenTimeObject.day"
        @click="onclickConvertDate"
        >Đổi ngày</v-btn>
      </v-col>
      <v-col cols="12" v-if="resultDate">
        <v-card
            class="mx-auto"
            color="transparent"
            rounded
            elevation="15"
        >
          <v-card-text>
            <div>Ngày kết quả</div>
            <p class="text-h4 text--primary">
              {{resultDate}}
            </p>
            <p>Tiết khí ngày {{`${this.chosenTimeObject.day}/${this.chosenTimeObject.month}/${this.chosenTimeObject.year}`}}</p>
            <p class="text-h5 text--primary">
              {{airData}}
            </p>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import moment from 'moment';
import 'moment-lunar';
import julian from 'julian'
import { displaySunLongitude } from '../amlich-hnd/amlich-hnd'

export default {
  name: 'HelloWorld',
  data: () => ({
    ready: false,
    fixedTimeObject: {
      daysOfMonth: [],
      monthsOfYear: ["01", "02", "03", "04", "05", "06", "07", "08", "09", "10", "11", "12"],
      years: []
    },
    chosenTimeObject: {
      day: "",
      month: "",
      year: ""
    },
    option: "1",
    resultDate: "",
    airData: "",
    dayRules:[v => !!v || 'Yêu cầu nhập ngày trước khi đổi']
  }),
  methods: {
      onclickConvertDate() {
        const dateToConvert = moment(`${this.chosenTimeObject.day}${this.chosenTimeObject.month}${this.chosenTimeObject.year}`, 'DDMMYYYY')
        if (this.option === '1') {
          this.resultDate = dateToConvert.lunar().format('DD/MM/YYYY')
        } else {
          this.resultDate = dateToConvert.solar().format('DD/MM/YYYY')
        }

        const dateJulian = julian(new Date(`${this.chosenTimeObject.year}-${this.chosenTimeObject.month}-${this.chosenTimeObject.day}T12:00:00.000Z`));
        console.log(new Date(`${this.chosenTimeObject.year}-${this.chosenTimeObject.month}-${this.chosenTimeObject.day}T12:00:00.000Z`));
        console.log(dateJulian);
        // const dateJulian = julian(new Date('1995-04-13T12:00:00.000Z'));

        this.airData = `Tiết ${displaySunLongitude(parseInt(dateJulian))}`
      },
      getDaysOfMonth(month) {
        const daysOfMonth = [];
        const now = new Date();
        const totalDays = new Date(now.getFullYear(), month + 1, 0).getDate();
        for (let i = 1; i <= totalDays; i++) {
          if (i < 10) {
            daysOfMonth.push(String(i).padStart(2, '0'));
          } else {
            daysOfMonth.push(String(i));
          }
        }
        return daysOfMonth;
      },
      setDefaults() {
        // generate years and set to v-select
        const years = [];
        for(let i = 2100; i >= 1900; i--) {
          years.push(`${i}`)
        }
        this.fixedTimeObject.years = years;
        // generate date based on year and month
        const today = new Date();
        this.fixedTimeObject.daysOfMonth = this.getDaysOfMonth(today.getMonth());
        // set date time today

        this.chosenTimeObject.year = `${today.getFullYear()}`
        this.chosenTimeObject.month = String(today.getMonth() + 1).length < 2 ? String(today.getMonth() + 1).padStart(2, '0') : String(today.getMonth() + 1 );
        this.chosenTimeObject.day = String(today.getDate()).length < 2 ? String(today.getDate()).padStart(2, '0') : String(today.getDate());
      },
      resetDaysWhenMonthChanged(newValue, oldValue) {
        if (newValue !== oldValue) {
          this.fixedTimeObject.daysOfMonth = this.getDaysOfMonth(Number(newValue) - 1);
          this.chosenTimeObject.day = ''
        }
      }
  },
  mounted() {
    this.setDefaults();
    this.$watch('chosenTimeObject.month', this.resetDaysWhenMonthChanged)
  }
};
</script>
<style scoped>
.link {
  padding-left: 5px;
  padding-right: 5px;
}
</style>